/*global*/
/*

body * {
    border: 2px solid red;
}
*/
body {
    height: 100vh;
    background-image: linear-gradient( 339.6deg, rgba(156,252,248,1) 11.2%, rgba(110,123,251,1) 91.1% );
}
button {
    outline: none;
}

/*Display*/
#display-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 13px;
}
.display {
    font-family: VT323, monospace;
    font-size: 1.8rem;
    color: #e7e7e7;
    background: rgb(0,0,0);
    background: linear-gradient(180deg, rgba(0,0,0,1) 47%, rgb(27, 41, 86) 100%);
    width: 320px;
    height: 70px;
    margin-top: 30px;
    padding-top: 10px;
    border: 5px solid rgba(19, 18, 18, 0.99);
    border-radius: 20px;
}
.display-text {
    animation: fadeIn 5s;
    animation-iteration-count: infinite;
    opacity: 0.98;
    font-size: 1.8rem;
    text-align: center;
}
.now-playing-text {
    animation: fadeIn 5s;
    animation-iteration-count: infinite;
    opacity: 0.98;
    text-align: center;
    font-size: 0.9rem;
}
@keyframes fadeIn {
    0% { opacity: 0.7; }
    50% { opacity: 0.8; }
    100% { opacity: 0.7; }
}
.volume-text {
    font-size: 1rem;
    text-align: left;
    padding-left: 15px;
    opacity: 50%;
}

/*Main Class*/
#drum-machine {
    top: 50%;
    left: 50%;
    width: 515px;
    height: 610px;
    transform: translate(-50%, -50%);
    background: rgb(91,90,90);
    background: linear-gradient(180deg, rgba(91,90,90,1) 0%, rgba(23,27,33,1) 4%, rgba(0,0,0,1) 96%, rgba(37,37,37,1) 100%);
    border-radius: 15%;
    position: fixed;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
/*drums*/
#drums {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 20px;
    justify-content: center;
    align-items: center;
    grid-gap: 30px;
    color: lightgray;
    width: 480px;
}
.OFF {
    color: #3f3f3f !important;
}
.drum-pad {
    color: inherit;
    font-size: 1rem;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: 3px solid rgba(40, 38, 38, 0.99);
    background: rgb(52,61,75);
    background: radial-gradient(circle, rgba(52,61,75,1) 0%, rgba(0,0,0,1) 33%);
    cursor: pointer;
}
/*for change when drums clicked AND pressed*/
.drum-pad:active, .drum-pad-press {
    font-size: 1rem;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background: rgb(0,0,0);
    background: radial-gradient(circle, rgba(0,0,0,1) 45%, rgb(43, 47, 51) 85%);
    border: 2px solid #5e5e5e;
    color: #2a2a2a;
}
/*Controls*/
#controls {
    width: 500px;
    color: #414141;
    font-size: 1.4rem;
    font-weight: bold;
    height: 40px;
    background: rgb(59,59,59);
    background: linear-gradient(171deg, rgba(59,59,59,1) 0%, rgba(23,27,33,1) 7%, rgba(0,0,0,1) 100%, rgba(37,37,37,1) 100%);
    margin: 0 10px 0 10px;
    border: 1px solid #171717;
    border-radius: 20%;
}


#controls button {
    background: rgb(0, 0, 0);
    background: linear-gradient(180deg, rgba(0, 0, 0, 1) 47%, rgb(9, 27, 49) 100%);
    width: 30px;
    color: #888888;
    font-size: 0.8rem;
    padding: 3px;
    border-radius: 20%;
    border: 1px solid #434343;
    cursor: pointer;
}
#power {
    float: right;
    text-align: right;
    padding-right: 80px;
    margin-right: 0;
}
#power button {
    width: 30px;
    padding: 5px;
}
#power button:active {
    border-color: white;
}
#volume {
    float: left;
    padding-left: 80px;
}
#volume button:active {
    border-color: white;
}

#footer {
    text-align: center;
    color: #0c0c0c;
    font-size: 0.98rem;
    text-shadow: 0 1px 0 #313131;
}


